import React from "react";
import styled from 'styled-components';
import SolarPergolaImg from '../assets/homepage/solar-roof.jpg';
import SafeImg from '../assets/powerroof/safe.jpg';
import SpectacularImg from '../assets/powerroof/spectacular.jpg';
import ReliableImg from '../assets/powerroof/reliable.jpg';
import { Parallax } from "react-parallax";
import Fade from 'react-reveal/Fade';
import Grid from '@mui/material/Grid';
import { animateScroll as scroll } from 'react-scroll';
import Footer from "../components/Footer";
import ScrollToTopIcon from '../components/ScrollToTopIcon';
import BannerVideo from "../assets/powerroof/power-roof-banner.mp4";
import { LazyLoadComponent } from 'react-lazy-load-image-component';
import Navbar from "../components/Navbar";
import axios from 'axios';
import { ReactMarkdown } from "react-markdown/lib/react-markdown";

class PowerRoof extends React.Component {
  state = {
    hide: false,
    activeTab: -1,
    scroll: false,
    bottom: false,
    content: {},
  }
  rootRef = React.createRef();
  tabsRef = React.createRef();
  safeRef = React.createRef();
  spectacularRef = React.createRef();
  reliableRef = React.createRef();
  _isMounted = false

  componentDidMount() {
    this._isMounted = true;
    axios.get('https://arkacms.xyz/api/mw-power-roof-pages')
      .then(res => {
        // console.log(res.data);
        let data = [...res.data.data];
        let content = {};
        data.forEach(item => {
          content[item?.attributes?.label] = item?.attributes?.richContent || item?.attributes?.content;
        });
        this.setState({ content: { ...content } });
        // console.log(content)
      })
      .catch(err => {
        console.log('Error : ', err)
      });
    scroll.scrollToTop({
      duration: 400,
      smooth: 'easeInOut'
    });
    window.addEventListener('scroll', this.onScroll, false);
  }
  onScroll = (e) => {
    if (window.scrollY > 350) {
      this.setState({ hide: true });
    } else {
      this.setState({ hide: false });
    }

    if (window.scrollY > window.innerHeight * 1.5) {
      let sections = [this.safeRef, this.spectacularRef, this.reliableRef]
      let anyVisible = false
      sections.forEach((ref, index) => {
        if (ref.current) {
          var rect = ref.current.getBoundingClientRect();
          var visible = (rect.top > -1 && rect.top < window.innerHeight / 2) || (rect.bottom > window.innerHeight / 2 && rect.bottom - window.innerHeight < window.innerHeight / 2);
          if (visible) {
            anyVisible = true;
            this.setState({ activeTab: index });
          }
        }
      });
      if (!anyVisible) {
        this.setState({ activeTab: -1 });
      }
    }

    // let that = this;
    // let scroll = window.scrollY;
    // if (scroll > 100 && that._isMounted) that.setState({ scroll: true });
    // else if (that._isMounted) that.setState({ scroll: false });
    // if (((window.innerHeight + window.pageYOffset + 50) >= document.body.offsetHeight) && that._isMounted)
    //   that.setState({ bottom: true })
    // else if (that._isMounted)
    //   that.setState({ bottom: false })
  }
  componentWillUnmount() {
    this._isMounted = false;
    window.removeEventListener('scroll', this.onScroll, false);
  }
  render() {
    let content = this.state.content;
    return (
      <Wrapper ref={this.rootRef}>

        <Navbar containerRef={this.rootRef} position="fixed" hide={this.state.hide} />

        <Parallax
          bgImage={null}
          bgImageAlt="solar roof"
          strength={300}
          renderLayer={percentage => (
            <div ref={this.bannerRef} style={{ minHeight: "150vh", background: 'rgba(0,0,0,0.4)', position: 'relative' }}>
              <LazyLoadComponent>
                <HeroVideo poster={SolarPergolaImg} loop playsInline autoPlay muted style={{ width: 'auto', height: '150vh', zIndex: '-1', mixBlendMode: 'multiply', position: 'absolute' }}>
                  <source src={BannerVideo} type="video/mp4" />
                </HeroVideo>
              </LazyLoadComponent>
              <Banner style={{ marginTop: `${(percentage - 1) * 45}%` }}>
                <Fade left duration={1000}>
                  <FlexContainer style={{ width: 'max-content', justifyContent: 'flex-start', maxWidth: '80vw', fontSize: '4rem', margin: '2rem 0' }}>{content?.heroText}</FlexContainer>
                </Fade>
                <Fade opposite bottom delay={500} duration={500}>
                  <FlexContainer style={{ width: '30rem', justifyContent: 'flex-start', maxWidth: '80vw', fontSize: '1.75rem', lineHeight: '2' }}>{content?.heroSubtext}</FlexContainer>
                </Fade>
                <Fade opposite delay={1000} duration={1000}>
                  <PaddedText>
                    <YOButton>
                      {content?.heroButtonText}
                    </YOButton>
                  </PaddedText>
                </Fade>
              </Banner>
            </div>
          )}
        >
        </Parallax>


        <Bar>
          <div style={{ background: 'white', color: 'black', padding: '0 2rem', display: 'inline-flex', width: 'max-content', height: '100%', alignItems: 'center', justifyContent: 'center' }}>
            <b>POWER ROOF</b>
          </div>
          <div style={{ display: 'inline-flex', width: 'auto', padding: '0 2rem', height: '100%', alignItems: 'center', position: 'relative' }}>
            <NavLink onClick={() => { this.setState({ activeTab: 0 }); this.safeRef.current.scrollIntoView({ behavior: 'smooth' }) }} active={String(this.state.activeTab === 0)}>
              Safe
            </NavLink>
            <NavLink onClick={() => { this.setState({ activeTab: 1 }); this.spectacularRef.current.scrollIntoView({ behavior: 'smooth' }) }} active={String(this.state.activeTab === 1)}>
              Spectacular
            </NavLink>
            <NavLink onClick={() => { this.setState({ activeTab: 2 }); this.reliableRef.current.scrollIntoView({ behavior: 'smooth' }) }} active={String(this.state.activeTab === 2)}>
              Reliable
            </NavLink>
          </div>
        </Bar>

        <div style={{ height: 'maxContent', background: '#001020' }} ref={this.safeRef}>
          <Grid container style={{ color: 'white' /*, flexWrap: 'wrap-reverse'*/, height: '100%' }}>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <FlexContainer style={{ paddingTop: '2rem' }}>
                <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={250} duration={250}>
                    {content?.sectionOneHead}
                  </Fade>
                </PaddedText>
                <PaddedText style={{ fontSize: '1.5rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={750} duration={250}>
                    <ReactMarkdown escapeHTML={false} >
                      {content?.sectionOneContent}
                    </ReactMarkdown>
                  </Fade>
                </PaddedText>
              </FlexContainer>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <Fade bottom delay={500} duration={250}>
                <LazyLoadComponent>
                  <ImageContainer src={SafeImg} />
                </LazyLoadComponent>
              </Fade>
            </Grid>
          </Grid>
        </div>

        <div style={{ height: 'maxContent', background: '#001020' }} ref={this.spectacularRef}>
          <Grid container style={{ color: 'white', flexWrap: 'wrap-reverse', height: '100%' }}>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <Fade bottom delay={500} duration={250}>
                <LazyLoadComponent>
                  <ImageContainer src={SpectacularImg} />
                </LazyLoadComponent>
              </Fade>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <FlexContainer style={{ paddingTop: '2rem' }}>
                <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={250} duration={250}>
                    {content?.sectionTwoHead}
                  </Fade>
                </PaddedText>
                <PaddedText style={{ fontSize: '1.5rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={750} duration={250}>
                    <ReactMarkdown escapeHTML={false} >
                      {content?.sectionTwoContent}
                    </ReactMarkdown>
                  </Fade>
                </PaddedText>
              </FlexContainer>
            </Grid>
          </Grid>
        </div>

        <div style={{ height: 'maxContent', background: '#001020' }} ref={this.reliableRef}>
          <Grid container style={{ color: 'white' /*, flexWrap: 'wrap-reverse'*/, height: '100%' }}>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <FlexContainer style={{ paddingTop: '2rem' }}>
                <PaddedText style={{ fontSize: '2.75rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={250} duration={250}>
                    {content?.sectionThreeHead}
                  </Fade>
                </PaddedText>
                <PaddedText style={{ fontSize: '1.5rem', fontWeight: '300', width: '90%' }}>
                  <Fade bottom delay={750} duration={250}>
                    <ReactMarkdown escapeHTML={false} >
                      {content?.sectionThreeContent}
                    </ReactMarkdown>
                  </Fade>
                </PaddedText>
              </FlexContainer>
            </Grid>
            <Grid item xs={12} sm={12} md={6} lg={6} xl={6}>
              <Fade bottom delay={500} duration={250}>
                <LazyLoadComponent>
                  <ImageContainer src={ReliableImg} />
                </LazyLoadComponent>
              </Fade>
            </Grid>
          </Grid>
        </div>
        <Footer rf={this.exploreRef} />

        <ScrollToTopIcon scroll={this.state.scroll} bottom={this.state.bottom} />
      </Wrapper >
    );
  }
}

export default PowerRoof;

const Wrapper = styled.div`
  width: 100%;
`;

const Bar = styled.nav`    
  top: 0;
  z-index:200;
  position:sticky;

  height:3rem;
  width:100%;
  padding:0;
  
  background: linear-gradient(45deg, #000 0%, #024 100%);  
  transition:all 0.5s ease-in-out;
  
  box-shadow:0 4px 4px 0 rgba(0,0,0,0.6);
  display: flex;
`;

const NavLink = styled.div`
  margin:0 1rem;
  padding:0.5rem 0;
  position:relative;
  outline: none;
  
  color:white;
  font-size: 1rem;
  font-weight: 500;
  text-decoration:none;
  
  transition:all 0.2s ease-in-out;
  cursor:pointer;

  &:after{
    content: '';
    position:absolute;
    left:0;
    bottom:0.25rem;
    height:${props => props.active === 'true' ? '2px' : '0.5px'};
    width:${props => props.active === 'true' ? '100%' : '0'};
    margin-left:${props => props.active === 'true' ? '0' : '50%'};
    transition: all 0.1s ease;
    background-color: ${props => props.active === 'true' ? 'orange' : 'orange'};
  }

  &:hover{
    color:orange;
    &:after{
      width:100%;
      margin-left: 0;
      box-shadow:${props => props.active === 'true' ? 'none' : '2px 2px 2px 0px rgba(255,255,255,0.8)'};
    }
  }
`;



const HeroVideo = styled.video`
  width: auto;
  height: 150vh;
  z-index: -1;
  mix-blend-mode: multiply;
  position: absolute;
  
  @media only screen and (max-width : 1260px) {
    left: -20vw;
  }  
  @media only screen and (max-width : 900px) {
    left: -60vw;
  }
  @media only screen and (max-width : 600px) {
    left: -200vw;
  }
`;

const Banner = styled.div`
  padding: 3rem 5vw;
  position: absolute;
  width: 90vw;
  top: 35%;
  left: 50%;
  transform: translate(-50%,-35%);
  color: white;
`;

const FlexContainer = styled.div`
  display: flex;
  align-items: center;
  /* justify-content: center; */
  flex-direction: column;
`;

const PaddedText = styled.div`
  padding: 1rem 0;
  line-height: 2;
`;

