//   width: 100%;
//   min-height: 100vh;
//   transition: 0.5s all ease-out;
//   background: ${props => props.position ? (props.position === 1 ? (`linear-gradient(90deg, #113 0%, #001 100%)`) : (`linear-gradient(90deg, #001 0%, #113 100%)`)) : (`linear-gradient(90deg, #001 0%, #113 100%)`)};
//   margin-left: ${props => props.position < props.active ? '-100%' : (props.position > props.active ? '100%' : '0')};
