const ImageContainer = styled.div`
  background:red;
  background: url(${props => props.src});
  margin:0;
  display: flex;
  width: 100%;
  height: 100vh !important;
  background-size: cover;
  background-position: center;
`;

const YOButton = styled.button`
  background: #0007;
  color: orange;
  border: 2px solid orange;
  padding: 1rem 2rem;
  font-size: 1.5rem;
  z-index: 100;
  transition: all 0.3s ease;
  cursor: pointer;

  &:hover {
    background: orange;
    color: black;
  }
`;